'use client';

import Link from 'next/link';
import Image from 'next/image';
import { useState, useEffect } from 'react';
import { useTranslations, useLocale } from 'next-intl';
import { usePathname, useRouter } from 'next/navigation';
import { routing, localeNames } from '@/i18n/routing';

export default function Navigation() {
  const t = useTranslations('nav');
  const tBanner = useTranslations('banner');
  const tMetals = useTranslations('metals');
  const locale = useLocale();
  const pathname = usePathname();
  const router = useRouter();
  
  const [isScrolled, setIsScrolled] = useState(false);
  const [metalsOpen, setMetalsOpen] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [langOpen, setLangOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => setIsScrolled(window.scrollY > 20);
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Body scroll lock when mobile menu is open
  useEffect(() => {
    if (mobileMenuOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
    return () => { document.body.style.overflow = 'unset'; };
  }, [mobileMenuOpen]);

  const switchLocale = (newLocale: string) => {
    const pathWithoutLocale = pathname.replace(`/${locale}`, '') || '/';
    router.push(`/${newLocale}${pathWithoutLocale}`);
    setLangOpen(false);
  };

  const navLinks = [
    { href: `/${locale}/metals`, label: t('metals'), hasDropdown: true },
    { href: `/${locale}/buy-sell`, label: t('buySell') },
    { href: `/${locale}/staking`, label: t('staking') },
    { href: `/${locale}/trust-center`, label: t('trustCenter') },
    { href: `/${locale}/how-it-works`, label: t('howItWorks') },
  ];

  const metalLinks = [
    { href: `/${locale}/metals/auxg`, symbol: 'AUXG', name: tMetals('gold'), icon: '/metals/gold.png' },
    { href: `/${locale}/metals/auxs`, symbol: 'AUXS', name: tMetals('silver'), icon: '/metals/silver.png' },
    { href: `/${locale}/metals/auxpt`, symbol: 'AUXPT', name: tMetals('platinum'), icon: '/metals/platinum.png' },
    { href: `/${locale}/metals/auxpd`, symbol: 'AUXPD', name: tMetals('palladium'), icon: '/metals/palladium.png' },
  ];

  return (
    <>
      {/* Soft Launch Banner */}
      <div style={{
        background: 'linear-gradient(to right, rgba(120,53,15,0.9), rgba(146,64,14,0.9), rgba(120,53,15,0.9))',
        color: '#FDE68A',
        padding: '10px 16px',
        textAlign: 'center',
        fontSize: '13px',
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        zIndex: 60,
        letterSpacing: '0.02em'
      }}>
        <span style={{ display: 'inline-flex', alignItems: 'center', gap: '10px', flexWrap: 'wrap', justifyContent: 'center' }}>
          <span style={{ width: '6px', height: '6px', background: '#FBBF24', borderRadius: '50%', boxShadow: '0 0 8px #FBBF24' }}></span>
          <span style={{ fontWeight: 500 }}>{tBanner('softLaunch')}</span>
          <span className="hide-mobile" style={{ color: '#FCD34D', opacity: 0.7 }}>•</span>
          <span className="hide-mobile" style={{ opacity: 0.9 }}>{tBanner('limits')}</span>
        </span>
      </div>

      {/* Main Navigation */}
      <nav style={{
        position: 'fixed',
        top: '38px',
        left: 0,
        right: 0,
        zIndex: 50,
        transition: 'all 0.3s ease',
        background: isScrolled ? 'rgba(10,15,28,0.98)' : 'transparent',
        backdropFilter: isScrolled ? 'blur(20px)' : 'none',
        borderBottom: isScrolled ? '1px solid rgba(255,255,255,0.05)' : 'none'
      }}>
        <div style={{ maxWidth: '1280px', margin: '0 auto', padding: '0 24px' }}>
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', height: '72px' }}>
            {/* Logo */}
            <Link href={`/${locale}`} style={{ display: 'flex', alignItems: 'center', gap: '12px', textDecoration: 'none' }}>
              <Image 
                src="/auxite-wallet-logo.png" 
                alt="Auxite" 
                width={140} 
                height={40} 
                style={{ objectFit: 'contain' }}
              />
            </Link>

            {/* Desktop Navigation */}
            <div className="desktop-nav" style={{ display: 'flex', alignItems: 'center', gap: '36px' }}>
              {navLinks.map((link) => (
                <div 
                  key={link.href} 
                  style={{ position: 'relative' }}
                  onMouseEnter={() => link.hasDropdown && setMetalsOpen(true)}
                  onMouseLeave={() => link.hasDropdown && setMetalsOpen(false)}
                >
                  <Link 
                    href={link.href} 
                    style={{ 
                      color: '#A1A1AA', 
                      textDecoration: 'none', 
                      fontWeight: 500, 
                      fontSize: '14px',
                      transition: 'color 0.2s',
                      display: 'flex',
                      alignItems: 'center',
                      gap: '4px'
                    }}
                  >
                    {link.label}
                    {link.hasDropdown && (
                      <svg style={{ width: '12px', height: '12px', transition: 'transform 0.2s', transform: metalsOpen ? 'rotate(180deg)' : 'rotate(0)' }} fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                      </svg>
                    )}
                  </Link>
                  
                  {/* Dropdown */}
                  {link.hasDropdown && metalsOpen && (
                    <div style={{
                      position: 'absolute',
                      top: '100%',
                      left: '-20px',
                      paddingTop: '12px',
                    }}>
                      <div style={{
                        background: 'rgba(20,27,45,0.98)',
                        backdropFilter: 'blur(20px)',
                        border: '1px solid rgba(255,255,255,0.08)',
                        borderRadius: '16px',
                        padding: '8px',
                        minWidth: '220px',
                        boxShadow: '0 20px 40px rgba(0,0,0,0.4)'
                      }}>
                        {metalLinks.map((metal) => (
                          <Link 
                            key={metal.href} 
                            href={metal.href}
                            style={{
                              display: 'flex',
                              alignItems: 'center',
                              gap: '12px',
                              padding: '12px 16px',
                              borderRadius: '10px',
                              textDecoration: 'none',
                              transition: 'background 0.2s'
                            }}
                          >
                            <Image src={metal.icon} alt={metal.name} width={32} height={32} />
                            <div>
                              <div style={{ color: 'white', fontWeight: 600, fontSize: '14px' }}>{metal.symbol}</div>
                              <div style={{ color: '#71717A', fontSize: '12px' }}>{metal.name}</div>
                            </div>
                          </Link>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </div>

            {/* Language Switcher & Sign In */}
            <div className="desktop-nav" style={{ display: 'flex', alignItems: 'center', gap: '16px' }}>
              {/* Language Switcher */}
              <div 
                style={{ position: 'relative' }}
                onMouseEnter={() => setLangOpen(true)}
                onMouseLeave={() => setLangOpen(false)}
              >
                <button style={{
                  background: 'rgba(255,255,255,0.05)',
                  border: '1px solid rgba(255,255,255,0.1)',
                  color: '#A1A1AA',
                  padding: '8px 12px',
                  borderRadius: '8px',
                  fontSize: '13px',
                  fontWeight: 500,
                  cursor: 'pointer',
                  display: 'flex',
                  alignItems: 'center',
                  gap: '6px'
                }}>
                  {locale.toUpperCase()}
                  <svg style={{ width: '10px', height: '10px' }} fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                  </svg>
                </button>
                
                {langOpen && (
                  <div style={{
                    position: 'absolute',
                    top: '100%',
                    right: 0,
                    paddingTop: '8px',
                  }}>
                    <div style={{
                      background: 'rgba(20,27,45,0.98)',
                      backdropFilter: 'blur(20px)',
                      border: '1px solid rgba(255,255,255,0.08)',
                      borderRadius: '12px',
                      padding: '6px',
                      minWidth: '120px',
                      boxShadow: '0 10px 30px rgba(0,0,0,0.4)'
                    }}>
                      {routing.locales.map((loc) => (
                        <button
                          key={loc}
                          onClick={() => switchLocale(loc)}
                          style={{
                            display: 'block',
                            width: '100%',
                            padding: '10px 14px',
                            background: loc === locale ? 'rgba(226,184,87,0.1)' : 'transparent',
                            border: 'none',
                            borderRadius: '8px',
                            color: loc === locale ? '#E2B857' : '#A1A1AA',
                            fontSize: '13px',
                            fontWeight: loc === locale ? 600 : 400,
                            cursor: 'pointer',
                            textAlign: 'left'
                          }}
                        >
                          {localeNames[loc]}
                        </button>
                      ))}
                    </div>
                  </div>
                )}
              </div>

              {/* Sign In Button */}
              <Link href="https://wallet.auxite.io" style={{
                background: 'linear-gradient(135deg, #E2B857 0%, #F5D77E 50%, #E2B857 100%)',
                color: '#0B1121',
                padding: '10px 24px',
                borderRadius: '10px',
                fontWeight: 600,
                fontSize: '14px',
                textDecoration: 'none',
                boxShadow: '0 4px 20px rgba(226,184,87,0.3)',
                transition: 'all 0.2s'
              }}>
                {t('signIn')}
              </Link>
            </div>

            {/* Hamburger Button */}
            <div 
              className={`hamburger ${mobileMenuOpen ? 'open' : ''}`}
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
            >
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
        </div>
      </nav>

      {/* Mobile Menu */}
      <div className={`mobile-menu ${mobileMenuOpen ? 'open' : ''}`}>
        {/* Language Switcher Mobile */}
        <div style={{ display: 'flex', gap: '8px', flexWrap: 'wrap', marginBottom: '16px' }}>
          {routing.locales.map((loc) => (
            <button
              key={loc}
              onClick={() => { switchLocale(loc); setMobileMenuOpen(false); }}
              style={{
                padding: '8px 14px',
                background: loc === locale ? 'rgba(226,184,87,0.15)' : 'rgba(255,255,255,0.03)',
                border: loc === locale ? '1px solid rgba(226,184,87,0.3)' : '1px solid rgba(255,255,255,0.05)',
                borderRadius: '8px',
                color: loc === locale ? '#E2B857' : '#A1A1AA',
                fontSize: '12px',
                fontWeight: loc === locale ? 600 : 400,
                cursor: 'pointer'
              }}
            >
              {loc.toUpperCase()}
            </button>
          ))}
        </div>

        {navLinks.map((link) => (
          <Link 
            key={link.href} 
            href={link.href}
            onClick={() => setMobileMenuOpen(false)}
          >
            {link.label}
          </Link>
        ))}
        
        {/* Mobile Metal Links */}
        <div style={{ marginTop: '16px', paddingTop: '16px', borderTop: '1px solid rgba(255,255,255,0.1)' }}>
          <div style={{ color: '#71717A', fontSize: '12px', marginBottom: '12px', textTransform: 'uppercase', letterSpacing: '0.1em' }}>{t('metals')}</div>
          <div style={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: '12px' }}>
            {metalLinks.map((metal) => (
              <Link 
                key={metal.href} 
                href={metal.href}
                onClick={() => setMobileMenuOpen(false)}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  gap: '10px',
                  padding: '12px',
                  background: 'rgba(255,255,255,0.03)',
                  borderRadius: '12px',
                  textDecoration: 'none',
                  border: '1px solid rgba(255,255,255,0.05)'
                }}
              >
                <Image src={metal.icon} alt={metal.name} width={28} height={28} />
                <div>
                  <div style={{ color: 'white', fontWeight: 600, fontSize: '14px' }}>{metal.symbol}</div>
                  <div style={{ color: '#71717A', fontSize: '11px' }}>{metal.name}</div>
                </div>
              </Link>
            ))}
          </div>
        </div>

        {/* Mobile Sign In */}
        <Link 
          href="https://wallet.auxite.io" 
          onClick={() => setMobileMenuOpen(false)}
          style={{
            marginTop: 'auto',
            background: 'linear-gradient(135deg, #E2B857 0%, #C9A227 100%)',
            color: '#0B1121',
            padding: '16px 24px',
            borderRadius: '12px',
            fontWeight: 600,
            fontSize: '16px',
            textDecoration: 'none',
            textAlign: 'center',
            boxShadow: '0 8px 32px rgba(226,184,87,0.3)'
          }}
        >
          {t('signIn')}
        </Link>
      </div>
    </>
  );
}
